      胡歌，1982年9月20日出生于上海市徐汇区，中国内地男演员、歌手。
      1996年，14岁的胡歌便成为上海教育电视台的小主持人，2005年毕业于上海戏剧学院表演系，同年在仙侠剧《仙剑奇侠传》中塑造了“李逍遥”一角，并演唱该剧插曲《六月的雨》《逍遥叹》 [1-2]  。2006年8月遭受严重车祸，2007年6月复出 [3]  。2009年主演仙侠剧《仙剑奇侠传三》 [4]  。2010年主演的穿越剧《神话》在央视八套播出 [5]  。2011年参演历史题材电影《辛亥革命》提名大众电影百花奖“最佳新人奖” [6-7]  。2012年主演玄幻剧《轩辕剑之天之痕》 [8]  。2013年主演两部话剧，凭借《如梦之梦》获得北京丹尼国际舞台表演艺术奖“最佳男演员奖” [9]  。，2018年凭借都市剧《猎场》二度提名上海电视节白玉兰奖“最佳男主角奖” [19-22]  。
